function Door(){
    return (<div style={{textAlign:"center" , fontSize:'200%'}}>
        <h1>Explicação</h1>
        Quando você escolhe uma porta o jogo escolhe uma porta que não seja a que voce escolheu e abre ela, se a porta aberta pelo jogo for errada ele te da a opção de abrir outra porta. Caso você escolha trocar de porta você esta adicionando a probabilidade que você possuia essa nova escolha, como inicialmente você tem a probabilidade de 2/3 de errar ,agora com a saida de uma das portas erradas você pode escolher de novo so que agora com mais 1/3 de chance de acerto, como inicialmente você tem a probabilidade que você tinha 1/3 de chance agora você tem 2/3 de chance de acertar se trocar de porta. 
    </div>);
}

function Calendar(){
    return (<div style={{textAlign:"center" , fontSize:'200%'}}>
        <h1>Explicação</h1>
        Quando você escolhe um numero de pessoas o que você pode não ter percebido e que você relaciona a data de aniversario de uma pessoa com a data de aniversario de todas as outras pessoas ,digamos que você tenha escolido 15 pessoas, no caso você esta correlacionando o aniversario de cada pessoa com cada uma das 14 pessoas restantes. Os calculos matematicos por outro lado ficam mais faceis de serem explciados caso você veja sendo a probabilidade de não dar certo. Digamos que voce esolha uma pessoa a probabilidade dela não ter o mesmo aniversario de outra pessoa é de 365/365, pois há 365 dias em um ano e só esxiste uma pessoa neste caso, agora se botarmos outra pessoa ,a probabilidade de não existir outra pessoa com mesmo aniversario é de (364!)/((365 - n)! * 365 ** (n-1)).
    </div>);
}

function Coin(){
    return (<div style={{textAlign:"center" , fontSize:'200%'}}>
        <h1>Explicação</h1>
        Neste jogo o problema esta relacionado a quem começa escolhendo as moedas, quando o jogador escolhe a sequencia de moedas, o algoritimo do site vai simplismente pegar a sequencia do jogador escolhida e modifica-la para que ele tenha mais chance de ganhar do que a sequencia escolhida pelo jogador, um exemplo simples de como isso acontece é: Se o jogado escolhe cara cara cara como sua sequencia o algoritimo escolhe coroa cara cara como sua sequencia, agora o unico jeito de o jogador ganhar é caso venha 3 caras nas primeiras 3 moedas, pois se cair uma coroa nesse cenario, o algotimo so precisará de duas caras pra ganhar enquanto o jogador ncessitará de 3. O algoritimo funciona mudando invertendo a ultima moeda e botando-a como primeira moeda, e a primeira e segunda moeda viram respectivamente, segunda e terceiras moedas.
    </div>)
}


function Resolution(props) {
    switch(props.page){
        case "Calendar":
            return Calendar();
        case 'Coin':
            return Coin();
        case 'Door':
            return Door();
    }
}

export default Resolution
