import DoorImg from "../components/DoorSvg.js"
import Tutorial from "./Tutorials.js"
import Resolution from "./Resolution.js"

function PortArray(ls,divIndex){
    
    const ports=[<button style={{height:"900px",maxHeight:'100vh',backgroundColor:"#732240",border:"0"}} key={0} value={0} onClick = {(e) => ls.onClickStart(e)} ls={ls}><DoorImg></DoorImg></button>,
    <button style={{height:"900px",maxHeight:'100vh',backgroundColor:"#732240",border:"0"}} key={1} value={1} onClick = {(e) => ls.onClickStart(e)} ls={ls}><DoorImg></DoorImg></button>,
    <button style={{height:"900px",maxHeight:'100vh',backgroundColor:"#732240",border:"0"}} key={2} value={2} onClick = {(e) => ls.onClickStart(e)} ls={ls}><DoorImg></DoorImg></button>];
    if(divIndex!==-1){
        ports[divIndex] = (<div style={{width:"33%",height:"300px",maxHeight:"50%",margin:"auto",paddingTop:"50%",color:"white"}}>
            Porta Errada<br />
            Deseja Trocar de porta?
            <button style={{align:"center"}} value={true} onClick={(e) => ls.onClickChoice(e)}>Sim</button>
            <button style={{margin:"center"}} value={false} onClick={(e) => ls.onClickChoice(e)}>Não</button>
        </div>)
    }
    return ports
}

function Ending(message,ls){
    
    switch(message){
        case "Você perdeu na primeira porta":
            return(<div style={{textAlign:"center"}}>
                <h1>Perdeu</h1>
                <h2>Não deu sorte</h2>
                <h3>A primeira porta aberta foi a certa</h3>
                <button onClick={() =>{ls.setGameState("But Why?")}}>Mas porquê?</button>
            </div>);
        case "Ganhou mudando de porta":
            return(<div style={{textAlign:"center"}}>
                <h1>Ganhou</h1>
                <h2>Genio da probabilidade</h2>
                <h3>Voce acertou em mudar de porta</h3>
                <button onClick={() =>{ls.setGameState("But Why?")}}>Mas porquê?</button>
            </div>);
        case "Ganhou sem mudar de porta":
            return(<div style={{textAlign:"center"}}>
                <h1>Ganhou</h1>
                <h2>Você deu muita sorte</h2>
                <h3>Voce acertou sem mudar de porta</h3>
                <button onClick={() =>{ls.setGameState("But Why?")}}>Mas porquê?</button>
            </div>);
        case "Perdeu trocando de porta":
            return(<div style={{textAlign:"center"}}>
                <h1>Perdeu</h1>
                <h2>Mas você fez o certo</h2>
                <h3>A primeira porta que você escolheu foi a certa</h3>
                <button onClick={() =>{ls.setGameState("But Why?")}}>Mas porquê?</button>
            </div>);
        case "Perdeu sem trocar de porta":
            return(<div style={{textAlign:"center"}}>
                <h1>Perdeu</h1>
                <h2>E foi algo esperado</h2>
                <h3>A primeira porta que você escolheu foi a certa</h3>
                <button onClick={() =>{ls.setGameState("But Why?")}}>Mas porquê?</button>
            </div>);

        default:
            return(<div>On Construction</div>);
    }
}

function DoorVisual(ls){
    switch(ls.gameState){
        case "Start":
            return ( <div style={{display: "grid",gridTemplateColumns:"1fr 1fr 1fr"}}>{PortArray(ls,-1)}</div>);
        case "First Door Lose":
            return(Ending("Você perdeu na primeira porta",ls))
        case "Choose":
            return(<div style={{display: "grid",gridTemplateColumns:"1fr 1fr 1fr"}}>{PortArray(ls,ls.doorToOpen)}</div>)
        case "Changed Win":
            return (Ending("Ganhou mudando de porta",ls));
        case "Win":
            return (Ending("Ganhou sem mudar de porta",ls))
        case "Changed Lose":
            return (Ending("Perdeu trocando de porta",ls))
        case "Lose":
            return (Ending("Perdeu sem trocar de porta"))
        case "But Why?": 
            return(<Resolution page={"Door"}></Resolution>)
        case "Tutorial":
            return(<Tutorial page={"Door"}/>)
        default:
            return(<div>Hmmm</div>)
    }
}

export default DoorVisual
