
import Handler from "../visualLogic/CoinSvgHandler"
function CoinSvg({value,onClick,settings}) {
  let click = () => {} 
  if(typeof onClick === "function")
    click = onClick;
  const [style,d] = Handler(settings);
  const svgStyle={
    maxHeight:"100vh"
  }
  if(settings==="Heads Silver"){
    svgStyle.paddingTop="10vh";
    svgStyle.paddingBottom="10vh";
  }
  return (
        <svg
          style={svgStyle}
          data-name="Layer 1"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 24 24"
          onClick={(e) => click(e)}
        >
          <defs>
          </defs>
          <circle
            cx={12}
            cy={12}
            r={9}
            stroke={style.stroke}
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            fill={style.fill}
          />
          
          <path
                style={style}
                d={d}
            />
        </svg>
  );
}

export default CoinSvg
