import React from 'react'

function SideBar(props) {
    return (
        <div className="mainSidebar" style={props.style}>
            <div id="mySidebar" className="sidebar">
                <a href="#/">Home</a>
                <a href={"#/"+props.name1}>{props.name1}</a>
                <a href={"#/"+props.name2}>{props.name2}</a>
                <a onClick={() => {window.location.reload()}} >Restart</a>
                <a onClick={() => props.resolution()}>Explicação</a>
                <a onClick={() => props.tutorial()}>Tutorial</a>
            </div>
            <div id="main">
                {props.children}
            </div>
        </div>
    )
}

export default SideBar
