
function Calendar(){
    return (<div>
        <h1>Jogo do calendario</h1>
        <h3>O jogo consiste em tentar adivinhar a probabilidade de pelo menos duas pessoas terem o mesmo aniversario <br/> dado um conjunto de N pessoas (que tambem é dado por você).</h3>
        <h2>Como Jogar:</h2>
        <h3>A primeira caixa é para você dar o numero de pessoas e o segundo para a probabilidade que você acha ser a certa.</h3>
    </div>)
}

function Coin(){
    return (<div>
        <h1>Jogo das moedas</h1>
        <h3>No jogo é gerado uma sequencia de moedas aleatorias tendo seus estados como cara ou coroa,<br/> o objetivo do jogo e você acertar uma sub sequencia de 3 moedas da sequencia aleatoria,<br /> antes da sequencia do oponente.</h3>
        <h2>Como Jogar:</h2>
        <h3>Na primeira pagina do jogo voce escolhe a sequencia desejada, a sequencia inicia sempre em 3 caras<br/> e cada vez que voce clica no icone de cara ou coroa, ele adiciona a moeda selecionada ao fim da sequencia <br/> e retira a primeira moeda, quando você clicar em terminar você será levado a pagina do jogo em si.<br/> Lá tudo terá uma sequencia de 3 moedas gerada automaticamente que seria do seu oponente,<br/> então tudo que você tem que fazer para gerar a sequencia é clicar em começar e a sequencia aleatoria será gerada.</h3>
    </div>)
}

function Door(){
    return (<div>
        <h1>Jogo das Portas</h1>
        <h3>Neste jogo existem 3 portas, duas erradas e uma certa, o seu objetivo no jogo é adivinhar a porta certa.</h3>
        <h2>Como Jogar:</h2>
        <h3>Você escolhe uma porta, o jogo abri uma porta que não foi a escolhida por você se a porta que ele abrir for a certa, você perde, se não ele te pergunta se você quer trocar de porta.</h3>
    </div>)
}


function Tutorials(props) {
    switch (props.page){
        case "Calendar":
            return Calendar();
        case 'Coin':
            return Coin();
        case 'Door':
            return Door();
    }
        
}

export default Tutorials
