import {useState} from 'react'
import SideBar from '../components/SideBar.js'
import CoinVisual from '../visualLogic/CoinVisual.js'

const coinInverse = (coin) =>{
    if(coin==="t")
        return "h"
    else
        return "t"
}
const getRandomInt = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

function timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}



function Coin(){
    const [bot,setBot] = useState("hhh");
    const [player,setPlayer] = useState("hhh");
    const [random,setRandom] = useState("");
    const [gameState,setGameState] = useState("Start")
    const [clicked,setClicked] = useState(false);
    const ls = { // Logic State
        bot:bot,
        player:player,
        setBot:setBot,
        setPlayer:setPlayer,
        random:random,
        setRandom:setRandom,
        gameState:gameState,
        setGameState:setGameState,
        clicked:clicked,
        setClicked:setClicked,
        onCoin: (e) => {
            let side = e.target.style.type;
            if(side===undefined)
                side = "h"
           ls.setPlayer(ls.player[1]+ls.player[2]+side);
        },
        onLockSequence: () => {
            ls.setBot(coinInverse(ls.player[2])+ls.player[0]+ls.player[1]);
            ls.setGameState("Sequence Locked")
        },
        tick:async function tick(){
            let sequence = ls.random;
            let counter = 0;
            console.log(ls.clicked)
            if(ls.clicked===false){
                ls.setClicked(true);
                while(counter<300){
                    sequence = sequence + (getRandomInt(0,1) ? "h" : "t");
                    ls.setRandom(sequence);
                    if(sequence.includes(ls.player)){
                        ls.setGameState("Player Wins")
                        return;
                    }else if(sequence.includes(ls.bot)){
                        ls.setGameState("Bot Wins");
                        return;
                    }
                    await timeout(1500);
                    counter++;
                }
            }
        }
    }

    return(
        <SideBar  name1={"Porta"} name2={"Calendario"} style={{ backgroundColor:"gray"}} resolution={
            () =>{
                setGameState('But Why?');
            }
        }
        tutorial = {
            () =>{
                setGameState('Tutorial');
            }
        }
        >
            {CoinVisual(ls)}
        </SideBar>
        )
}




export default Coin
