import React from 'react'
import "../App.css"
import Calendar from "../components/CalendarSvg.js"
import Door from "../components/DoorSvg.js"
import Coin from "../components/CoinSvg.js"

function Home(){
    return (
        <div className="grid-container">
            <a className="fit" style={{backgroundColor:"white"}} href="#/Porta">
                <Door value={true}></Door>
            </a>
            <a className="fit" style={{backgroundColor:"#5F7373"}} href="#/Calendario">
                <Calendar></Calendar>
            </a>
            <a className="fit" style={{backgroundColor:"#343F40"}} href="#/Moeda">
                    <Coin settings={"Heads Silver"}></Coin>
            </a>
        </div>
    )
}

export default Home
